.row {
    color: white;
    margin-left: 10px;
}

.row__poster {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row__poster::-webkit-scrollbar {
    display:none;
}

.card-img-top {
    max-height: 100px;
    object-fit: contain;
    max-height: 100px;
    margin-right: 10px;
    /* Delay effect */
    transition: transform 450ms; 
}

.card-img-top:hover {
    transform: scale(1.08);
    opacity:1;
}